<template>
    <div>
        <div class="row" v-if="message.show">
            <div class="col-md-12">
                <vuestic-alert :type="message.type" :withCloseBtn="true">
                    <span class="badge badge-pill badge-success">{{message.title}}</span>
                    {{message.text}}
                    <i class="fa fa-close alert-close"></i>
                </vuestic-alert>
            </div>
        </div>
        <div class="row">
            <div class="col-md-12">
                <widget headerText="List Settings">
                    <data-table :apiUrl="apiUrl"
                                :tableFields="tableFields"
                                :itemsPerPage="itemsPerPage"
                                :sortFunctions="sortFunctions"
                                :apiMode="apiMode"
                                :httpOptions="httpOptions"
                                @gotoDetail="gotoDetail"
                                @deleteRow="deleteRow"
                                :reloadTable="reloadTable"
                                :paginationPath="paginationPath">
                    </data-table>
                </widget>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import Widget from '../vuestic-components/vuestic-widget/VuesticWidget'
    import DataTable from '../vuestic-components/vuestic-datatable/VuesticDataTable'
    import BadgeColumn from '../tables/BadgeColumn.vue'
    import FieldsDef from './data/fields'
    import ItemsPerPageDef from '../vuestic-components/vuestic-datatable/data/items-per-page-definition'

    Vue.component('badge-column', BadgeColumn);
    import SettingSerice from 'services/setting';

    export default {
        components: {
            DataTable,
            Widget
        },
        name: 'Table',
        data () {
            return {
                apiUrl: window.apiUrl + '/setting',
                apiMode: true,
                itemsPerPage: ItemsPerPageDef.itemsPerPage,
                tableFields: FieldsDef.tableFields,
                sortFunctions: FieldsDef.sortFunctions,
                paginationPath: '',
                httpOptions: {
                    withCredentials: true
                },
                reloadTable: false,
                message: {
                    show: false,
                    type: "success",
                    text: ""
                }
            }
        },
        methods: {
            gotoDetail(rowData){
                this.$router.push(`setting/${rowData._id}`);
            },
            deleteRow(rowData){
                SettingSerice
                    .deleteItem(rowData._id)
                    .then((res) => {
                        if (res) {
                            let message = {
                                show: true,
                                type: "success",
                                title: "Success",
                                text: "Delete Successful"
                            };
                            this.message = message;

                            this.reloadTable = true;
                        }
                    })
            }
        }
    }
</script>

<style lang="scss">

    .color-icon-label-table {
        td:first-child {
            width: 1rem;
        }
    }
</style>



