import Axios from 'axios';

const save = (formData) => {
    "use strict";
    if (formData.status) {
        formData.status = 1;
    } else {
        formData.status = 0;
    }
    let id = formData.id;

    if (id !== undefined) {
        // UPDATE
        return Axios
            .post(`${window.apiUrl}/setting/${id}`, formData,
                {
                    withCredentials: true
                }
            )
            .then((res) => {
                return res;
            });
    } else {
        // SAVE NEW
        return Axios
            .post(`${window.apiUrl}/setting`, formData,
                {
                    withCredentials: true
                }
            )
            .then((res) => {
                return res;
            });
    }

};

const findById = (id) => {
    "use strict";
    return Axios
        .get(`${window.apiUrl}/setting/${id}`,
            {
                withCredentials: true
            }
        )
        .then((res) => {
            if (res.status == 200) {
                return res.data;
            }
        });
};

const deleteItem = (id) => {
    "use strict";
    return Axios
        .delete(`${window.apiUrl}/setting/${id}`, {
            withCredentials: true
        })
        .then((res) => {
            if (res.status == 200) {
                return true;
            } else {
                return false;
            }
        })

};

export  default  {
    save,
    findById,
    deleteItem
}


